.app {
  background: #209cee;
  padding-top: 30px;
  height: 97vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo-list {
  background: #e8e8e8;
  border-radius: 4px;
  padding: 5px;
  width: 400px;
}

.todo {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 3px 10px;
  font-size: 18px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
}

.buttons {
  display: flex;
}

button {
  border: 1px solid #6b63da;
  margin: 2px;
  font-weight: 1500;
  font-size: 18px;
  border-radius: 4px;
  background-color: #6b63da;
}

button:hover {
  cursor: pointer;
}

label {
  padding-right: 10px;
  font-size: 18px;
}

input {
  padding: 2px;
  border: 1px solid gray;
  border-radius: 4px;
  width: 240px;
}

@media only screen and (max-width: 900px) {
  .todo-list {
    width: 90%;
  }
}

@media only screen and (max-width: 768px) {
  .todo {
    font-size: 13px;
  }
  
  button {
    font-size: 13px;
  }
}